import React from 'react';
import PropTypes from 'prop-types';

import cx from 'classnames';

import _map from "lodash/map";
import _unique from "lodash/uniq";
import _size from "lodash/size";

import config from 'config';

import styles from './Authors.module.scss';

function Authors({authors}) {
  
  const renderAuthor = (author) => {
    const authorData = authorsMetadata[author]
    return (
      <a
        href={authorData.url}
        className={styles.author}
        target="_blank"
        rel="noopener noreferrer"
        key={author}
      >
        {authorData.name}
      </a>
    );
  }
  
  const renderInstitute = (institute) => {
    return (
      <div className={styles.institute} key={institute}>
        {config.institutes[institute]}
      </div>
    );
  }
  
  
  const authorsMetadata = config.authorsMetadata;
  const institutes = _unique(_map(authors, (author) => authorsMetadata[author].institute));
  
  const isMultipleInstitutes = _size(institutes) > 1;
  
  return (
    <div className={styles.authorsContainer}>
      <div className={styles.list}>
        {_map(authors, renderAuthor)}
      </div>
      <div className={cx(styles.list, {[styles.multipleInstitutes]: isMultipleInstitutes})}>
        {_map(institutes, renderInstitute)}
      </div>
    
    </div>
  );
}

Authors.propTypes = {};

export default Authors;